import {h} from 'preact';

/**
 * Component Properties.
 */
interface LinkProps
{
	active: boolean;
	children?: JSX.Element[];
	onClick( event: Event ): void;
}

/**
 * Ссылка-переключатель.
 */
function Link(
	{active, children, onClick}: LinkProps,
): JSX.Element
{
	
	return (
		<button type="button" 
			aria-pressed={String(active)}
			onClick={onClick}
		>
			{children}
		</button>
	);
}

/**
 * Module.
 */
export {
	Link as default,
	LinkProps,
};



