import {h} from 'preact';
import AddTodo from './AddTodo';
import Footer from './Footer';
import TodoList from './TodoList';

function TodoApp(): JSX.Element
{
	return (
		<div>
			<h1>todo list</h1>
			<AddTodo />
			<TodoList />
			<Footer />
		</div>
	);
}

/**
 * Корень приложения.
 */
// tslint:disable-next-line:variable-name
const AppRoot = <TodoApp />;

/**
 * Module.
 */
export {
	AppRoot as default,
	TodoApp,
};



